<template>
  <div>
    <div class="flex flex-row" style="height: 100vh">
      <div class="basis-4/4">
        <h1></h1>
      </div>
      <div
        class="basis-1/4 h-14 bg-fuchsia-500 shadow-lg rounded-lg"
        style="height: 100vh"
      >
        <button @click="moveBack">BACK</button>
      </div>
      <div class="basis-1/2">
        <CardItem
          :key="currObj.id"
          :Caption="currObj.name"
          :BGColor="currObj.bg"
        />
      </div>
      <div class="basis-1/4 bg-fuchsia-500 shadow-lg rounded-lg flex">
        <button @click="moveNext">NEXT</button>
      </div>
    </div>
    <!-- <div class="scene flex">
     
      
    </div> -->
    <!-- <CardItem
        style="width: 100px; height: 100px"
        v-for="col in json"
        :key="col.id" 
        :BGColor="col.bg"
      /> -->
    <!-- <div
      style="width: 100px; height: 100px"
      v-for="col in json"
      :key="col.id"
      :style="{ 'background-color': col.bg }"
    >
      {{ col.name }}
      <span> </span>
    </div> -->
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
// import {cDicts} from '@/data/colorsCards.js'
import json from "../data/colors.Cards.json";
import CardItem from "./CardItem.vue";

export default defineComponent({
  setup() {
    return { json };
  },
  data() {
    return {
      currObj: json[0],
      count: 0,
    };
  },
  components: { CardItem },
  methods: {
    moveBack() {
      if (this.count === 0) {
        this.count = json.length - 1;
      } else {
        this.count = this.count - 1;
      }
      this.currObj = this.json[this.count];
    },
    moveNext() {
      if (this.count === json.length - 1) {
        this.count = 0;
      } else {
        this.count = this.count + 1;
      }
      this.currObj = this.json[this.count];
    },
  },
});
</script>

<style scoped>
.scene {
  display: flex;
  height: 100vh;
  background-color: gray;
}
</style>
